import React, { lazy, Suspense, useEffect, useState } from "react";
import {
  Container,
  Typography,
  Grid,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  TextField,
  CircularProgress,
  IconButton,
  Divider,
} from "@material-ui/core";
import api from "../api/api";
import baseurl from "../api/baseurl";
import { connect } from "react-redux";
import { Close } from "@material-ui/icons";

const Login = lazy(() => import("../components/Login"));
const Registration = lazy(() => import("../components/Registration"));
const RetailProduct = ({ auth, profile }) => {
  const [product, setProduct] = useState([]);
  const [openMainDialog, setOpenMainDialog] = useState(false);
  const [values, setValues] = useState({});
  const [openSuccessDialog, setOpenSuccessDialog] = useState(false);
  const [openLoginDialog, setOpenLoginDialog] = useState(false);
  const [openRegistrationDialog, setOpenRegistrationDialog] = useState(false);
  const [formValues, setFormValues] = useState({
    quantity: 0,
  });
  useEffect(() => {
    document.title = "Retail Products";
    fetchProducts();
  }, []);

  const fetchProducts = async () => {
    try {
      const response = await api.get(`/api/product`);
      return setProduct(response.data.data);
    } catch (err) {
      return;
    }
  };

  const submit = async () => {
    try {
      await api.post(`/api/product-order`, {
        product: values.id,
        ...formValues,
        customer: profile.id,
      });
      setFormValues({ quantity: 0 });
      setOpenSuccessDialog(true);
    } catch (err) {
      return alert("Sorry, something went wrong! Please try again.");
    }
  };

  const renderProducts = () => {
    if (product.filter((a) => a.status).length)
      return (
        <Grid container style={{ marginTop: 20 }} spacing={3}>
          {product
            .filter((a) => a.status)
            .map((a, i) => {
              return (
                <Grid
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setValues(a);
                    setOpenMainDialog(true);
                  }}
                  item
                  key={i}
                  xs={12}
                  sm={12}
                  md={6}
                  lg={3}
                  xl={3}
                >
                  <div
                    style={{
                      border: "1px solid #eee",
                      boxShadow: "1px 1px 8px #ddd",
                      padding: 0,
                    }}
                  >
                    <img
                      alt={a.name}
                      src={`${baseurl}${a.image}`}
                      style={{ objectFit: "contain", width: "100%" }}
                    />
                    <div
                      style={{
                        backgroundColor: "#333",
                        position: "sticky",
                        opacity: 0.8,
                        marginTop: -45,
                        padding: 10,
                      }}
                    >
                      <Typography style={{ color: "white", fontWeight: 500 }}>
                        {a.name}
                      </Typography>
                    </div>
                  </div>
                </Grid>
              );
            })}
        </Grid>
      );
  };

  const renderMainDialog = () => {
    if (values) {
      return (
        <Dialog
          scroll="body"
          fullWidth
          maxWidth="xs"
          open={openMainDialog}
          onClose={() => {
            setValues({});
            setFormValues({ quantity: 0 });
            setOpenMainDialog(false);
          }}
        >
          <div>
            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                width: "100%",
                position: "absolute",
              }}
            >
              <IconButton
                onClick={() => {
                  setValues({});
                  setOpenMainDialog(false);
                }}
              >
                <Close />
              </IconButton>
            </div>

            <div
              style={{
                display: "flex",
                justifyContent: "center",
                height: 350,
              }}
            >
              <img
                alt={values.name}
                src={`${baseurl}${values.image}`}
                style={{
                  objectFit: "contain",
                  width: "100%",
                }}
              />


            </div>

            <div style={{ padding: 10, marginTop: 10 }}>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                NAME (MOM)
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
                {values.mom_name}
              </Typography>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                Contact Person
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
                {values.contact_person}
              </Typography>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                District
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
                {values.district}
              </Typography>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                PHONE
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
                {values.phone}
              </Typography>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                EMAIL ADDRESS
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
                {values.email}
              </Typography>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                ADDRESS
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
                {values.address}
              </Typography>
              <Divider style={{ marginBottom: 10, marginTop: 10 }} />
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                PRODUCT NAME
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>{values.name}</Typography>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                PRICE
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
                {values.price}
              </Typography>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                FSSAI ID
          </Typography>
              <Typography style={{ fontWeight: 500, marginBottom: 8 }}>
                {values.fssai_id}
              </Typography>
              <Typography style={{ fontSize: 11, fontWeight: 400 }}>
                PRODUCT DESCRIPTION
          </Typography>
              <Typography style={{ whiteSpace: "pre-line" }}>
                {values.description}
              </Typography>
              <div
                style={{
                  display: "flex",
                  justifyContent: "flex-end",
                  marginTop: 20,
                }}
              >
                <Typography style={{ margin: 5 }}>
                  Quantity:
                </Typography>
                <TextField
                  type="number"
                  variant="outlined"
                  size="small"
                  style={{ width: 80, marginRight: 15 }}
                  value={formValues.quantity}
                  onChange={(e) => {
                    setFormValues({ ...formValues, quantity: e.target.value });
                  }}
                />

                <Button
                  disabled={formValues.quantity < 1 ? true : false}
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    if (auth) {
                      if (
                        window.confirm(
                          `Are you sure you want to order this product?`
                        )
                      ) {
                        return submit();
                      }
                    } else {
                      setOpenLoginDialog(true);
                    }
                  }}
                >
                  Place Order
                </Button>
              </div>
            </div>
          </div>
        </Dialog>
      );
    }
  };
  return (
    <Container maxWidth="md">
      <Typography variant="h5" gutterBottom style={{ fontWeight: 700, marginBottom: 20, marginTop: 80 }} color="primary">
        MOM Retail Products
          </Typography>

      {renderProducts()}
      {renderMainDialog()}


      <Dialog
        fullWidth
        maxWidth="xs"
        open={openSuccessDialog}
        onClose={() => {
          setOpenSuccessDialog(false);
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            padding: 20,
          }}
        >
          <img
            alt="Success"
            src="/success.png"
            style={{
              width: 80,
            }}
          />
          <Typography
            align="center"
            variant="h6"
            style={{
              color: "#009688",
            }}
          >
            Your order has been submitted successfuly
          </Typography>
          <Typography
            align="center"
            variant="h6"
            style={{
              color: "#009688",
            }}
          >
            We shall contact you soon
          </Typography>
          <Button
            onClick={() => {
              setOpenSuccessDialog(false);
            }}
            style={{ marginTop: 20, borderRadius: 50 }}
            variant="contained"
            color="primary"
          >
            OK
          </Button>
        </div>
      </Dialog>

      <Dialog
        fullWidth
        maxWidth="xs"
        open={openLoginDialog}
        onClose={() => {
          setOpenLoginDialog(false);
        }}
      >
        <DialogTitle>Login</DialogTitle>
        <DialogContent dividers>
          <Suspense
            fallback={
              <div
                style={{
                  marginTop: 400,
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <CircularProgress />
              </div>
            }
          >
            <Login
              setOpenRegistrationDialog={setOpenRegistrationDialog}
              setOpenLoginDialog={setOpenLoginDialog}
            />
          </Suspense>
        </DialogContent>
      </Dialog>
      <Dialog
        fullWidth
        maxWidth="xs"
        open={openRegistrationDialog}
        onClose={() => {
          setOpenRegistrationDialog(false);
        }}
      >
        <DialogTitle>Registration</DialogTitle>
        <DialogContent dividers>
          <Suspense
            fallback={
              <div
                style={{
                  marginTop: 400,
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <CircularProgress />
              </div>
            }
          >
            <Registration
              setOpenRegistrationDialog={setOpenRegistrationDialog}
              setOpenLoginDialog={setOpenLoginDialog}
            />
          </Suspense>
        </DialogContent>
      </Dialog>
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    auth: state.auth,
    profile: state.profile,
  };
};

export default connect(mapStateToProps, null)(RetailProduct);
